import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DetailsPageComponent } from './components/details-page/details-page.component';
import { FirstChildComponent } from './components/first-child/first-child.component';
import { HomepageComponent } from './components/homepage/homepage.component';
import { SecondpageComponent } from './components/secondpage/secondpage.component';

const routes: Routes = [
  {path:'',component:HomepageComponent},
  {path:'test',component:FirstChildComponent},
  {path:'mylist',component:SecondpageComponent},
  {path:'watch/:media_type/:id',component:DetailsPageComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
