import { Component, OnInit } from '@angular/core';
import {PostsService} from '../../services/posts.service';
@Component({
  selector: 'app-current-play-movies',
  templateUrl: './current-play-movies.component.html',
  styleUrls: ['./current-play-movies.component.css']
})
export class CurrentPlayMoviesComponent implements OnInit {

  public currentPlayMovies:any;
  
  constructor(private postsService:PostsService) { }

  ngOnInit(): void {
    this.fetchData();
  }
  fetchData(){
    this.postsService.getCurrentMovies().subscribe((res: any)=>{
      
      this.currentPlayMovies=res.slice(0, 5);
      // console.log(this.currentPlayMovies);
    })
  }

}
