import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-continue-watch',
  templateUrl: './continue-watch.component.html',
  styleUrls: ['./continue-watch.component.css']
})
export class ContinueWatchComponent implements OnInit {
  public data:any; 
 
  public formatted:any[] = [];
  constructor() { }

  ngOnInit(): void {
    // history
    let str = localStorage.getItem('history')
    var j = -1;
    let obj=str?JSON.parse(str):{arr:[]};
    this.data = []
    let arr = obj.arr.reverse()
    for(let i in arr){
      if(arr[i]) this.data.push(arr[i]);
    }
    var i = 0;
  this.formatted = []
for(var item in this.data){

  if (i % 6 == 0) {
    j++;
    this.formatted[j] = [];
    this.formatted[j].push(this.data[i]);
}
else {
    this.formatted[j].push(this.data[i]);
}

i++;
if(i==24){
  break;
}
}
  }

}
